body {
  margin: 0;
  padding: 0;
  font-family: Blender Pro, Lato, Lucida Grande, Tahoma, Sans-Serif;
  background-color: #171717;
  color: #42E9FF;
  color: #FCEE0A;
  color: #FF375C;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

a:hover {
  text-decoration: none;
}

footer {
  position: fixed;
  bottom: 6px;
  right: 12px;
}

.bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url(https://cyberpunk.net/build/images/home3/screen-image-city-732aff73.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: brightness(50%);
}

.wrapper {
  padding-left: 32px;
  padding-right: 32px;

  @media screen and (min-width: 1440px) {
    width: 1280px;
    margin: 0 auto;
  }
}

.showOnLoad {
  display: none;
}

.logo {
  background-image: url(https://upload.wikimedia.org/wikipedia/commons/e/e6/Cyberpunk_2077_logo.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 100%;
  height: 200px;
}

h1 {
  color: #42E9FF;
  text-shadow: 0 0 10px #42E9FF;
  text-align: center;
  text-transform: uppercase;
  margin: 48px 0;
}

.countdown {
  font-size: 98px;
  font-size: 7.5rem;
  text-shadow: 0 0 10px #FF375C;
  text-align: center;

  @media screen and (max-width: 1440px) {
    font-size: 48px;
  }

  &__column {
    display: inline-block;
    margin: 0 24px;

    span:first-of-type {
      font-weight: 100;
      font-size: 32px;
    }
  }
}

@keyframes glow {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #42E9FF, 0 0 40px #42E9FF, 0 0 50px #42E9FF, 0 0 60px #42E9FF, 0 0 70px #42E9FF;
  }

  to {
    text-shadow: 0 0 20px #fff, 0 0 30px #42E9FF, 0 0 40px #42E9FF, 0 0 50px #42E9FF, 0 0 60px #42E9FF, 0 0 70px #42E9FF, 0 0 80px #42E9FF;
  }
}
